import * as React from "react";
import * as Styles from "components/palette/ShapesPalette.css";
import {connect} from "react-redux";
import {PaletteItem} from "components/palette/PaletteItem";
import {IAppState, IPaletteAction} from "actions";
import {shapeFactory, ShapeType} from "components/shapes/Shapes";
import {selectShapeOnPalette} from "actions";

interface IProps {
  selectedShapeType?: ShapeType;
}

interface IReduxActions {
  selectShapeOnPalette(shapeType: ShapeType): IPaletteAction;
}

function mapStateToProps(state: IAppState): IProps {
  return {
    selectedShapeType: state.palette.selectedShapeType,
  };
}

class ShapesPalette extends React.Component<IProps & IReduxActions> {
  public render(): JSX.Element {
    return (
      <div className={Styles.palette}>
        {this.renderPaletteItem(ShapeType.Circle)}
        {this.renderPaletteItem(ShapeType.Square)}
        {this.renderPaletteItem(ShapeType.Triangle)}
      </div>
    );
  }

  private renderPaletteItem(shapeType: ShapeType): JSX.Element {
    const {selectedShapeType} = this.props;
    return (
      <PaletteItem shapeType={shapeType} onClick={this.clickHandler}>
        {shapeFactory(shapeType, {x: 20, y: 20, isSelected: selectedShapeType === shapeType})}
      </PaletteItem>
    );
  }

  private clickHandler = (shapeType: ShapeType) => {
    this.props.selectShapeOnPalette(shapeType);
  };
}

export default connect(
  mapStateToProps,
  {selectShapeOnPalette}
)(ShapesPalette);
