import * as React from "react";
import * as Styles from "components/palette/ShapesPalette.css";
import {ShapeType} from "components/shapes/Shapes";

interface IPaletteItemProps {
  shapeType: ShapeType;
  onClick(shapeType: ShapeType): void;
}

export class PaletteItem extends React.Component<IPaletteItemProps> {
  public render(): JSX.Element {
    return (
      <button onClick={this.clickHandler} className={Styles.paletteItem}>
        <svg width={100} height={100}>
          {this.props.children}
        </svg>
      </button>
    );
  }

  private clickHandler = () => {
    this.props.onClick(this.props.shapeType);
  };
}
