import * as React from "react";
import * as ReactDOM from "react-dom";
import {Provider} from "react-redux";
import {createStore} from "redux";

import {EditingPane} from "components/EditingPane";
import {IAppState, TAction} from "actions";

const initialState = {
  palette: {selectedShapeType: undefined},
};

function reducer(state: IAppState = initialState, action: TAction): IAppState {
  switch (action.type) {
    case "SELECT_SHAPE":
      return {
        palette: {
          selectedShapeType: action.shapeType,
        },
      };
  }
  return state;
}

const store = createStore(reducer);

ReactDOM.render(
  <Provider store={store}>
    <EditingPane />
  </Provider>,
  document.getElementById("editor")
);
