import * as React from "react";
import * as Styles from "components/shapes/Shapes.css";
import {IProps, ShapeSize} from "components/shapes/Shapes";

export class Square extends React.Component<IProps> {
  public render(): JSX.Element {
    const {x, y, isSelected} = this.props;
    const classNames = [Styles.shape];
    if (isSelected) {
      classNames.push(Styles.selected);
    }
    return <rect x={x} y={y} width={ShapeSize} height={ShapeSize} className={classNames.join(" ")} />;
  }
}

export default Square;
